import * as React from "react";
import { Admin,  Resource , ListGuesser, Layout ,EditGuesser} from 'react-admin';
import { Provider } from 'react-redux';
import { createHashHistory } from 'history';
import { UserList ,UserCreate, UserEdit} from './views/clients';
import { DogList, DogEdit, DogCreate } from './views/dogs';
import PeopleIcon from '@material-ui/icons/People';
import PetsIcon from '@material-ui/icons/Pets';
import Dashboard from './views/Dashboard';
import {authProvider,dataProvider, getFCMToken,onMessageReceved } from './components/authProvider';
import Utils from '@date-io/moment'
import GroupIcon from '@material-ui/icons/Group';
import MyLayout from './components/MyLayout';
import {BookingList} from './views/allbookings'
import muCustomRoutes from './components/muCustomRoutes'
import createAdminStore from './components/createAdminStore';
import { MuiPickersUtilsProvider } from '@material-ui/pickers'
import {PaymentList} from './views/payments'
import {NotificationContainer, NotificationManager} from 'react-notifications';
import 'react-notifications/lib/notifications.css';
const history = createHashHistory();


const App = () =>{


  const [isTokenFound, setTokenFound] = React.useState(false);
  getFCMToken(setTokenFound);
  
  onMessageReceved(payload=> {

    NotificationManager.success(payload.notification.body, payload.notification.title);
  })

 
  return (
  <Provider
        store={createAdminStore({
            authProvider,
            dataProvider,
            history,
        })}
    >
        <MuiPickersUtilsProvider utils={Utils}>
        <NotificationContainer/>
     <Admin disableTelemetry layout={MyLayout} history={history} customRoutes={muCustomRoutes}   dashboard={Dashboard} dataProvider={dataProvider} authProvider={authProvider}>

        <Resource name="clients"  list={UserList}  icon={PetsIcon} create={UserCreate} to='/clients' edit={UserEdit} options={{ "label": "Clients"  }}  />
       <Resource name="dogs" list={DogList}  create={DogCreate} edit={DogEdit}  />
       <Resource name="dog_breeds" list={ListGuesser}     />
       <Resource name="bookings" list={BookingList}  edit={EditGuesser}   />
       <Resource name="services" list={ListGuesser}     />
       <Resource name="payments" list={PaymentList}     />
       
     </Admin>
     </MuiPickersUtilsProvider>
</Provider>
  );}

export default App;
