import React from "react";
import { ReactDOM, Link } from "react-dom";
import ReactDataGrid from "react-data-grid";
import "../styles.css";
import { dataProvider } from '../components/authProvider'
import Chip from '@material-ui/core/Chip'
import { Toolbar, Data, Filters } from "react-data-grid-addons";
import SendIcon from '@material-ui/icons/Send';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import {


  Button


} from 'react-admin';

var dateFormat = require("dateformat");

const selectors = Data.Selectors;
const {
  NumericFilter,
  AutoCompleteFilter,
  MultiSelectFilter,
  SingleSelectFilter
} = Filters;

const addonsChip = (params) => {
  if (params.value.length !== 0) {

    return (
      <>
        {params.value.map(item =>

          <Chip color='secondary' label={item} key={item} />
        )}
      </>
    )

  }
  else
    return (<span></span>)
}

const getDateRange = (params) => {

  return (<span >{dateFormat(params.row['start'], 'h:mm TT') || ''}-{dateFormat(params.value, 'h:mm TT') || ''
  }</span>)
}

const BookingDaateFormatter = (params) => {


  let date = dateFormat(params.value, "dddd dd/mm/yyyy");
  return (<span >{date}</span>)

}

const ServiceFormatter = (params) => {

  return (<span >{params.value || ''} {params.row.category || ''}</span>)
};

const LinktoClients = (params) => {

  if (params.row['client_ref']) {


    return (<a href={'#clients/' + params.row['client_ref']} >{params.value}</a>)

  }
  else
    return (<span >{params.value}</span>)

}
const LinktoDogs = (params) => {

  if (params.row['dog_ref']) {


    return (<a href={'#dogs/' + params.row['dog_ref']} >{params.value}</a>)

  }
  else
    return (<span >{params.value}</span>)

}



const columns = [
  { key: "name", name: "Name", resizable: true, formatter: LinktoClients, filterable: true, },
  { key: "dogName", name: "Dog", resizable: true, formatter: LinktoDogs, filterable: true, },
  { key: "service", name: "Service", resizable: true, formatter: ServiceFormatter, filterable: true, filterRenderer: AutoCompleteFilter },
  { key: "start", name: "Booking Date", resizable: true, formatter: BookingDaateFormatter, filterable: true, },
  { key: "end", name: "Time", resizable: true, formatter: getDateRange, filterable: true, },
  { key: "addOns", name: "AddOns", resizable: true, formatter: addonsChip, filterable: true, },
  { key: "code", name: "Booking Code", resizable: true, filterable: true, }
];


export default class todayDaycare extends React.Component {


  constructor(props) {
    super(props)

    this.state = {
      rows: [],
      selectedIndexes: [],
      filters: {}
    }
  }




  onRowsSelected = rows => {
    this.setState({
      selectedIndexes: this.state.selectedIndexes.concat(
        rows.map(r => r.rowIdx)
      )
    });
  };

  onRowsDeselected = rows => {
    let rowIndexes = rows.map(r => r.rowIdx);
    this.setState({
      selectedIndexes: this.state.selectedIndexes.filter(
        i => rowIndexes.indexOf(i) === -1
      )
    });
  };
  componentDidMount() {
    let today = new Date()
    dataProvider.getList('bookings', {filter: {'start': dateFormat(today, "yyyy-mm-dd"), 'category':'daycare'}, pagination: { page: 1, perPage: 100000 }} )
      .then(({ data }) => {
        this.setState({ rows: data })


      })
      .catch(error => {


      })

  }


  handleFilterChange = (filter, filters) => {
    const newFilters = { ...filters };
    if (filter.filterTerm) {
      newFilters[filter.column.key] = filter;
    } else {
      delete newFilters[filter.column.key];
    }
    return newFilters;
  };

  getValidFilterValues = (rows, columnId) => {
    return rows
      .map(r => r[columnId])
      .filter((item, i, a) => {
        return i === a.indexOf(item);
      });
  }

  getRows = (rows, filters) => {
    return selectors.getRows({ rows, filters });
  }


  render() {

    const filteredRows = this.getRows(this.state.rows, this.state.filters);
    return (

      <div style={{ height: '94%' }}>
        <ReactDataGrid
          columns={columns}
          rowGetter={i => filteredRows[i]}
          rowsCount={filteredRows.length}
          onColumnResize={(idx, width) =>
            console.log(`Column ${idx} has been resized to ${width}`)
          }
          rowSelection={{
            showCheckbox: true,
            enableShiftSelect: true,
            onRowsSelected: this.onRowsSelected,
            onRowsDeselected: this.onRowsDeselected,
            selectBy: {
              indexes: this.state.selectedIndexes
            }
          }}
          toolbar={<Toolbar enableFilter={true}>{this.state.selectedIndexes.length === 1 && <Button
          
            label="Charge card">
            <MonetizationOnIcon />
          </Button>}{this.state.selectedIndexes.length !== 0 && <Button
            label="Message Owners">
            <SendIcon />
          </Button>} </Toolbar>}
          onAddFilter={filter => this.setState({ filters: this.handleFilterChange(filter) })}
          onClearFilters={() => this.setState({ filters: {} })}
          getValidFilterValues={columnKey => this.getValidFilterValues(this.state.rows, columnKey)}
        />
      </div>
    );
  }
}

